<template>
    <div>
        <div class="kd-page-title">
            请输入您的姓名，点击查询
        </div>
        <div class="kd-page-content-wrapper">
            <div class="inputWrap">
                <kd-full-input
                    placeholder="请输入您的姓名"
                    :watch-data="getName"
                    ref="inputName"
                />
                <p class="error-tip">
                    {{ errorMsg }}
                </p>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!name"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { duplicateRes } from '../../../../../service/human/duplicate-name';
import { checkName } from '../../../../../utils/tools';
import { keyboardHide } from '@/service/common';
import audioObj from '@/assets/media/household/16.mp3';
import { playAudio } from '../../../../../utils/media';

export default {
    name: 'DuplicationNameInputName',
    data () {
        return {
            name: '',
            errorMsg: ''
        };
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        }),
        ...mapState('duplicateName', {})
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
        this.$refs.inputName.changeData(this.identifyCard.partyName);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('duplicateName', ['setDuplicateResult']),
        getName (value) {
            this.name = value;
        },
        async nextStep () {
            if (checkName(this.name)) {
                const res = await duplicateRes({
                    name: this.name
                });
                if (res && res.code === '0') {
                    if (res.result) {
                        this.setDuplicateResult(res.result);
                        this.changeStep('down');
                    } else {
                        this.changeTipPage({
                            message: '未查到重名！',
                            enableBack: true
                        });
                    }
                } else {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            } else {
                this.errorMsg = '姓名格式书写有误';
            }
        }
    },
    beforeDestroy () {
        this.audioObj.destroy();
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .inputWrap {
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;

        > div {
            color: #000000;
            width: 60%;
            height: 72px;
            margin-top: 100px;
        }
    }

    .error-tip {
        color: red;
        font-size: 25px;
        text-align: left;
    }
</style>
