import request from '@/utils/request';

// 重名查询  ---- 保存人脸识别信息
export const duplicateNameFace = (params) => {
    return request.ajax('/ssm/duplicateName/faceRecognition', 'post', params);
};

// 重名查询  ---- 保存人脸识别信息
export const duplicateRes = (params) => {
    return request.ajax('/ssm/duplicateName/getResult', 'get', params);
};
