<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <input-name v-if="currentStep === steps.findIndex(step=>step.key === 'inputName')"/>
        <result v-if="currentStep === steps.findIndex(step=>step.key === 'result')"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import inputName from './components/input-name';
import result from './components/result';
import { duplicateNameFace } from '@/service/human/duplicate-name';

export default {
    name: 'DuplicationNameIndex', // 重名查询
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        process,
        inputName,
        result
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        async handleBusiness () {
            const res = await duplicateNameFace({
                idNum: this.identifyCard.certNumber,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }

    },
    beforeMount () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '输入姓名',
                isActive: false,
                visible: true,
                key: 'inputName'
            },
            {
                name: '查询结果',
                isActive: false,
                visible: true,
                key: 'result'
            }
        ]);
    }
};
</script>

<style scoped lang="scss"></style>
