            查询结果
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <div class="title">
                {{ CONSTANT.locationName }}常住人口中<span>{{ duplicateResult.name }}</span>共有<span>{{ duplicateResult.total }}</span>人同名
            </div>
            <ul>
                <li>
                    <div><img :src="imgMan"></div>
                    <p>男性<span>{{ duplicateResult.maleNum }}</span>位</p>
                </li>
                <li>
                    <div><img :src="imgWoman"></div>
