<script>
import { mapState, mapMutations } from 'vuex';
import imgMan from '@/assets/images/default/man.png';
import imgWoman from '@/assets/images/default/woman.png';

export default {
    name: 'DuplicationNameResult',
    data () {
        return {
            name: '',
            imgMan: imgMan,
            imgWoman: imgWoman
        };
    },
    computed: {
        ...mapState('duplicateName', {
            duplicateResult: 'duplicateResult'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        getName (value) {
            this.name = value;
        },
        preStep () {
            this.changeStep('up');
        }
    }

};
</script>

